import { Component, OnInit } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';
import { AngularFireStorage } from 'angularfire2/storage';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';



@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {
  participantsCollectionRef: AngularFirestoreCollection<any>;
  participants: Observable<any[]>;
  participantcount: any;
  participant;
  selectedId:any;
  url: any;
  file: File;
  numbering=new Array;

  progressBarValue;
  model = {
    address: "",
    age: "",
    badge: "",
    body: "",
    city: "",
    country: "",
    date: "",
    designation: "",
    email: "",
    from: "",
    image: "assets/img/placeholder.png",
    institution: "",
    label: "",
    medical: "",
    member: "",
    nationality: "",
    paymentmode: "",
    pin: "",
    space: "",
    state: "",
    subject: "",
    subject2: "",
    telephone: "",
    whatsapp: "",
    size:"",
  }
  







  constructor(private afs: AngularFirestore, private storage: AngularFireStorage) {
    this.url = "../assets/img/placeholder.png";
    this.participantsCollectionRef = this.afs.collection('participants');

    this.participants = this.participantsCollectionRef.snapshotChanges().map((actions) => {
      return actions.map((action) => {
        const data = action.payload.doc.data();
        const id = action.payload.doc.id;
        return { id, ...data };
      })
    });



    this.participantsCollectionRef.valueChanges().subscribe((val) => {
      this.participantcount = val.length;
      for(var i=0; i<=this.participantcount; i++)
      {
         this.numbering.push(i);
      }   

    })


  }



  editparticipant(participant) {
    this.model = participant;
    this.selectedId = participant.id;
  }

  onFileChanged(event) { // called each time file input changes
    if (event.target.files && event.target.files[0]) {
      this.file = event.target.files[0]
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]); // read file as data url
      reader.onloadend = (event) => { // called once readAsDataURL is completed
        this.model.image = reader.result;
      }
    }
  }


  uploadpic() {
    let uniquekey = 'avi' + Math.floor(Math.random() * 1000000);
    const uploadtask = this.storage.upload('/participants/' + uniquekey, this.file);
    uploadtask.percentageChanges().subscribe((value) => {
      this.progressBarValue = value.toFixed(0);
      let progressbar: HTMLElement = document.getElementById('progressbar') as HTMLElement;
      progressbar.style.width = this.progressBarValue + '%';
      uploadtask.then((result) => {
        this.model.image = result.downloadURL;
        this.updaterecord()
      });
      uploadtask.catch((err) => {
        console.log(err);
      })
    })
  }


  updaterecord() {
    this.participantsCollectionRef.doc(this.selectedId).update(this.model)
    .then((data)=>{
     console.log(data);
    })
    .catch((err)=>{
     console.log(err)
    });
  }

delete(){
  this.participantsCollectionRef.doc(this.selectedId).delete();
}
  
  ngOnInit() { }


}



